    patientId: null,
    partnerId: null,
    semenDate: null,
    collection: null,
    abstinence: null,
    collectTime: null,
    recvTime: null,
    liquified: null,
    viscosity: null,
    ph: null,
    volume: null,
    volume1: null,
    volume2: null,
    concentration: null,
    concentration1: null,
    concentration2: null,
    totalCount: null,
    totalCount1: null,
    totalCount2: null,
    motility: null,
    motility1: null,
    motility2: null,
    totalMotility: null,
    totalMotility1: null,
    totalMotility2: null,
    progression: null,
    progression1: null,
    progression2: null,
    roundCells: null,
    roundCells1: null,
    roundCells2: null,
    agglutination: null,
    viability: null,
    fragmentation: null,
    antiAgg: null,
